.menu {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  background-color: #2c2c2c;
  height: 100vh;
  transition: opacity .3s ease-in-out, transform .3s ease-in-out, visibility 0s linear .3s;
  position: fixed;
  inset: 0;
  transform: translateX(-100%);
}

.menu:target {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
  transition: opacity .3s ease-in-out, transform .3s ease-in-out;
  transform: translateX(0);
}

.menu__nav {
  flex-direction: column;
  align-items: center;
  gap: 24px;
  display: none;
  position: absolute;
  top: 40%;
  left: 0;
  right: 0;
}

.menu:target .menu__nav {
  display: flex;
}

.menu__nav--main-text {
  text-decoration: none;
  transition: transform .3s;
  display: inline-block;
  position: relative;
}

.menu__nav--main-text:hover {
  transform: scale(1.2);
}

.menu__nav--main-text:after {
  content: "";
  background-color: #2060f6;
  border-radius: 4px;
  width: 0;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.menu__nav--main-text:hover:after {
  width: 100%;
}

body:has(.menu:target) {
  overflow: hidden;
}

body:has(.menu:target) .slider, body:has(.menu:target) .slider-desktop {
  visibility: hidden;
}

